@import url(https://fonts.googleapis.com/css?family=Material+Icons|Lato:400|Ubuntu:300,400,500,500i&subset=latin-ext);
@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  width: 100%;
  background-color: #fff;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  margin: 0 auto; }
  header .container {
    display: flex;
    align-items: center;
    height: 40px; }
    header .container a {
      font-family: "Lato", sans-serif;
      font-style: italic;
      font-size: 1rem;
      margin-left: 16rem;
      text-decoration: none; }
    header .container .logo {
      width: 1.8rem;
      height: 1.8rem; }
      header .container .logo img {
        width: 45px; }

form input {
  width: 80%;
  border: 1px solid #eaeaea;
  border-radius: 8px;
  padding: 6px;
  font-size: 0.8rem;
  outline: none;
  color: #333; }

button {
  background: transparent;
  cursor: pointer;
  box-shadow: none;
  border: none;
  color: #00b4ef;
  outline: none; }
  button i:hover {
    color: purple; }

.module {
  background-color: #ffffff;
  padding: 20px;
  box-shadow: 0 6px 15px 2px rgba(0, 0, 0, 0.1);
  margin: 40px auto;
  border-radius: 8px;
  width: 60%;
  position: relative;
  align-items: center; }

.find-city {
  display: flex;
  align-items: center; }

h2 {
  font-family: "Lato", sans-serif;
  font-size: 1rem;
  margin-bottom: 20px; }

li {
  display: flex;
  align-items: center;
  margin: 8px 0; }

.city {
  font-family: "Ubuntu", sans-serif;
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 10px; }

.temperature {
  font-size: 3rem;
  font-family: "Ubuntu", sans-serif;
  color: #333030;
  font-weight: 500; }

.weather {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-around;
  background-color: #ffffff;
  padding: 20px;
  box-shadow: 0 6px 15px 2px rgba(0, 0, 0, 0.1);
  margin: 40px auto;
  border-radius: 8px;
  width: 60%;
  position: relative; }

.conditions {
  width: 12%;
  height: 12%;
  align-items: center;
  margin-right: 3px; }

.weatherInfo {
  display: flex;
  flex-direction: column; }

.WrongVal {
  font-size: 20px;
  color: red;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-around;
  background-color: #ffffff;
  padding: 20px;
  box-shadow: 0 6px 15px 2px rgba(0, 0, 0, 0.1);
  margin: 40px auto;
  border-radius: 8px;
  width: 60%;
  position: relative;
  text-decoration: none; }

* {
  box-sizing: border-box;
  margin: 0; }

body {
  font-family: "Lato", sans-serif;
  background-color: #f8f8f8;
  background-image: url(/WeatherApp/static/media/clouds-2329680_1920.955a0a7b.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  width: 100vw;
  height: 100vh; }

